@import url('https://fonts.googleapis.com/css2?family=Edu+VIC+WA+NT+Beginner:wght@600&family=Pacifico&family=Poppins&family=Roboto:wght@100;300;400;500;700;900&display=swap');
@font-face {
    font-family: 'EF_hyunydororong';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2206-01@1.0/EF_hyunydororong.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

.letter{
    position: fixed;
    top:50%;
    left: 50%;
    z-index: 10;
    transform: translate(-50%,-50%);
}
.none {
    display: none;
}
.tempo{
    display: none;
}
.letter-border{
    position: fixed;
    top:50%;
    left: 50%;
    z-index: 10;
    transform: translate(-50%,-50%);
    border: 1px solid #000000;
    img{
        vertical-align: middle;
    }
}

.letter-content{
    font-family: 'EF_hyunydororong';
    font-size: 32px;
    font-weight: normal;
    font-style: normal;

    background-color: white;
    text-align: center;
    position: absolute;
    top: 48%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.write{
    margin-top: 20px;
}
.writebutton{
    margin-top: 90px;
}
button{
    cursor: pointer;
    width: 225px;
    height: 50px;    
    font-size: 27px;
    font-weight: bold;
    border-radius: 15px;
    border: 2px solid gray;
    color: hsl(0 0 0 / 0.7);
    background-color: transparent;

}
button:hover {
    border: 2.2px solid gray;
    font-size: 31px;
    color: hsl(0 0 0 / 0.7);
}