@import url('https://fonts.googleapis.com/css2?family=Pacifico&family=Poppins&family=Roboto:wght@100;300;400;500;700;900&display=swap');
:root {
    --bg-color: #FFF9CA;
    --typewriterSpeed: 5s;
    --typewriterCharacters: 24;
}

.layout{
    margin: 0;
    height: 100vh;
    background: url("../../image/back2.png");
    position: fixed;
    width: 100vw;
    z-index: 20;
}
.content1{
    height: 18%;
    width: 100%;
    // background-color: #FFDEB4;
    z-index: 2;
    .image1{
        width: 100%;
    }
}
.content2{
    height: 65%;
    width: 100%;
    // background-color: #FFDEB4;
}
.content3{
    height: 17%;
    width: 100%;
    // background-color: #B2A4FF;
    button{
        cursor: pointer;
        width: 225px;
        height: 50px;
        position: absolute;
        top: 90%;
        left: 50%;
        transform: translate(-50%,-50%);
        font-size: 27px;
        border-radius: 15px;
        border: 1px solid gray;
        color: hsl(0 0 0 / 0.7);
        background-color: transparent;

    }
    button:hover {
        border: 2px solid gray;
        font-size: 31px;
        color: hsl(0 0 0 / 0.7);
    }
}
.card-content{
    position: fixed;
    top:50%;
    left: 50%;
    z-index: 10;
    background-color: #fff;

    justify-content: center;
    transform: translate(-50%,-50%);
    img{
        width: 310px;
    }
    img:hover{
        width: 330px;
        cursor: pointer;
    }
}
.contents{
    position: absolute;
    top: 87%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: 'Pacifico', cursive;
    font-weight: bold;
    font-size: 30px;
    width: max-content;
}
h1::before,
h1::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

h1::before {
  background: white;
  border-radius: 10px;
  animation: typewriter var(--typewriterSpeed)
    steps(var(--typewriterCharacters)) 0.5s forwards;
}

h1::after {
  width: 0.12em;
  background: black;
  animation: typewriter var(--typewriterSpeed)
      steps(var(--typewriterCharacters)) 0.5s forwards,
    blink 750ms steps(var(--typewriterCharacters)) infinite;
}

@keyframes typewriter {
  to {
    left: 100%;
  }
}

@keyframes blink {
  to {
    background: transparent;
  }
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

#yt-link {
  position: absolute;
  bottom: 2em;
  width: 100%;
  color: hsl(0 0 0 / 0.7);

  &:hover,
  &:focus {
    color: teal;
  }
}
@media ( max-width:700px){
  .card-content{
    img{
        width: 230px;
    }
  }
}
